.topnav {
  background-color: #333;
  overflow: hidden;
  border-radius: 1.565vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.13vh;
  height: 11.738vh;
}
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
  font-size: 3.13vh;
  padding: 11.738vh 0;
}
.topnav a:not(:first-child) {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  padding-left: 2.504vh;
  padding-right: 2.504vh;
}
.topnav a:hover {
  background-color: #ddd;
  color: black;
}
.topnav a.logo {
  background-color: #aa0404;
  color: white;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.logoimage {
  width: 7.825vh;
  height: 7.825vh;
  padding: 1.565vh;
  margin: 0.313;
}
.tourdiv {
  display: grid;
  gap: 0;
  background-color: #dbdbdb;
  border: 0.158vh solid black;
  margin-bottom: 3.13vh;
  box-sizing: border-box;
}
.tourdiv:last-child {
  margin-bottom: 0;
}
.tourrow {
  display: grid;
  grid-template-columns: 1fr auto auto;
  border-bottom: 0.158vh solid black;
}
.tourrow:last-child {
  border-bottom: 0;
}
.tourinfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 1.565vh 1.565vh 0 1.565vh;
  height: 100%;
  box-sizing: border-box;
}
.buytickets {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 1.565vh;
}
.buytickets button {
  background-color: black;
  border: 0.156vh solid black;
  color: white;
  padding: 0.784vh 4.695vh;
}
.date {
  font-size: 1.878vh;
  margin-bottom: 0.784vh;
}
.city {
  font-size: 2.817vh;
  margin-bottom: 0.784vh;
}
.location {
  font-size: 2.191vh;
  padding-bottom: 0.784vh;
}
.tourname {
  font-size: 4.695vh;
  text-align: center;
  text-shadow: 0.313vh 0.313vh 0.784vh red;
  font-family: wolfhagen-blackletter;
  margin-top: 0;
}
@font-face {
  font-family: wolfhagen-blackletter;
  src: url("../../public/Fonts/wolfhagen_blackletter/WolfhagenBlackletter-Regular.ttf");
}
body {
  margin: 1.252vh;
}
@font-face {
  font-family: "OpenDyslexic";
  src: url("../../public/Fonts/opendyslexic/OpenDyslexic-Regular.woff2")
      format("woff2"),
    url("../../public/Fonts/opendyslexic/OpenDyslexic-Regular.woff")
      format("woff");
  font-weight: normal;
  font-style: normal;
}
.high-contrast {
  --background-color: #000000;
  --text-color: #ffffff;
  --link-color: #00ffff;
  --button-background: #ffcc00;
  --button-text: #000000;
  --border-color: #ffffff;
  background-color: var(--background-color) !important;
  color: var(--text-color) !important;
  border-color: var(--border-color) !important;
}
.high-contrast a {
  color: var(--link-color) !important;
  text-decoration: underline;
}
.high-contrast button,
.high-contrast input[type="button"],
.high-contrast input[type="submit"] {
  background-color: var(--button-background) !important;
  color: var(--button-text) !important;
}
.high-contrast .tourinfo,
.high-contrast .tourinfo span {
  color: #000000 !important;
}
.high-contrast .buytickets button {
  background-color: var(--button-background) !important;
  color: var(--button-text) !important;
  border: 0.1vh solid var(--border-color) !important;
}
.open-dyslexic {
  font-family: "OpenDyslexic", sans-serif !important;
}
.open-dyslexic input,
.open-dyslexic textarea,
.open-dyslexic button {
  font-family: "OpenDyslexic", sans-serif !important;
}
.open-dyslexic .tourname {
  font-family: "OpenDyslexic", sans-serif !important;
  text-shadow: none;
}
